import React, {Component} from 'react'
import './approval.css'
import '../../index.css'
import {TextArea, Icon, Button, Menu, Input, Modal, Image} from 'semantic-ui-react'
import 'semantic-ui-css/semantic.min.css'
import PointsItem from '@/components/pointsItem/PointsItem'
import TableItem from '@/components/tableItem/TableItem'
import ApprovalNode from '@/components/approvalNode/ApprovalNode'
import {Popover, Spin, Avatar} from 'antd'

export default class Approval extends Component {

    constructor() {
        super()

        this.state = {
            approvalContent: '',
            fastList: [
                '赞',
                'OK',
                '加油',
                '好的',
                '请及时完成'
            ],
            fastModalOpen: false,
            approvalIndex: 0,
            loading: true
        }
    }

    componentWillMount() {
        //初始化时，获取审批单据数据
    }

    componentDidMount() {
        setInterval(() => {
            this.setState({
                loading: false
            })
        }, 1500)
    }

    render() {
        const {approvalContent, fastList, fastModalOpen, approvalIndex} = this.state

        //审批常用语
        let fastItems = []
        for (let i = 0; i < fastList.length; i++) {
            fastItems.push(<div className='fastItem' onClick={
                this.fastSelect.bind(this, i)
            }>{fastList[i]}</div>)
        }


        let tableItems = []
        //主表字段
        for (let i = 1; i < 6; i++) {
            tableItems.push(<TableItem type={i + ""}></TableItem>)
        }

        //从表字段
        tableItems.push(<div className='gray-line' style={{height: '8px'}}></div>)
        tableItems.push(<div className='tableTitle'>从表明细</div>)
        for (let i = 1; i < 6; i++) {
            tableItems.push(<TableItem type={i + ""}></TableItem>)
        }

        //审批要点
        let pointItems = []
        pointItems.push(<div className='tableTitle'>审批要点</div>)
        for (let i = 1; i < 5; i++) {
            pointItems.push(<PointsItem type={i + ""}></PointsItem>)
        }

        //审批节点
        let approvalPoint = []

        approvalPoint.push(<div className='approval_tab_parent'>
            <div className={approvalIndex == 0 ?
                'approval_tab_selected' : 'approval_tab_normal'}
                 onClick={this.approvalTabSelect1}>审批节点
            </div>
            <div style={{background: '#cccccc', width: '1px', height: '20px'}}></div>
            <div className={approvalIndex == 1 ?
                'approval_tab_selected' : 'approval_tab_normal'}
                 onClick={this.approvalTabSelect2}>审批历史
            </div>
        </div>)

        for (let i = 0; i < 4; i++) {
            approvalPoint.push(<ApprovalNode></ApprovalNode>)
        }


        return <div style={{width: '100%', height: '100%'}}>
            <Spin size="large" style={{display: this.state.loading ? 'flex' : 'none'}}
                  tip='数据加载中...'>
            </Spin>

            <div className='root' style={{display: this.state.loading ? 'none' : 'flex'}}>
                < div className='content'>
                    <div className='headerLayout'>
                        <Avatar size={42} src={require('@/images/default_header.png')}/>
                        <div className='headerText'>饶猛的请假申请流程</div>
                    </div>
                    <div className='gray-line' style={{height: '1px'}}></div>
                    {tableItems}
                    <div className='gray-line' style={{height: '8px'}}></div>
                    {pointItems}
                    <div className='gray-line' style={{height: '8px'}}></div>
                    {approvalPoint}
                </div>

                <div className='bottomMenu'>
                    <div className='gray-line' style={{height: '8px'}}></div>
                    <div className='editLayout'>
                        <Icon disabled name='edit'/>
                        <TextArea className='editInput'
                                  value={approvalContent}
                                  placeholder='请输入审批意见...'
                                  onChange={this.approvalEdit}/>
                        {/*<Modal trigger={<div className='fastWords' onClick={this.fastClick}>常</div>}
                                   open={fastModalOpen}
                                   onClose={this.fastModalClose}
                                   size='small'>
                                <Modal.Content image>
                                    <Modal.Description>
                                        {fastItems}
                                    </Modal.Description>
                                </Modal.Content>
                            </Modal>*/}
                        <Popover title='常用语' trigger='click'
                                 content={fastItems}
                                 visible={fastModalOpen}
                                 onVisibleChange={this.handleFastVisiable}>
                            <div className='fastWords' onClick={this.fastClick}>常</div>
                        </Popover>
                    </div>
                    <div className='gray-line' style={{height: '8px'}}></div>
                    <div className='menuParent'>
                        <div className='menuItem' onClick={this.approvalAgree}>同意</div>
                        <div style={{background: '#cccccc', width: '1px', height: '24px'}}></div>
                        <div className='menuItem' onClick={this.approvalDisagree}>不同意</div>
                    </div>
                </div>
            </div>
        </div>
    }

    handleFastVisiable = (visible) => {
        this.setState({
            fastModalOpen: visible
        })
    }

    approvalTabSelect1 = () => {
        this.setState({
            approvalIndex: 0
        })
    }

    approvalTabSelect2 = () => {
        this.setState({
            approvalIndex: 1
        })
    }

    approvalAgree = () => {
        console.log('同意')
    }

    approvalDisagree = () => {
        console.log('不同意')
    }

    approvalEdit = (e) => {
        console.log(e.target.value)
        this.setState({
            approvalContent: e.target.value
        })
    }

    fastSelect = (index) => {
        const {approvalContent, fastList} = this.state
        this.setState({
            approvalContent: approvalContent + fastList[index],
            fastModalOpen: false
        })
    }

    fastClick = () => {
        this.setState({
            fastModalOpen: true
        })
    }

    fastModalClose = () => {
        this.setState({
            fastModalOpen: false
        })
    }
}
